import {
  CornerstoneImage,
  CornerstoneSingleImage,
  InsightViewer,
  installWADOImageLoader,
  unloadImage,
} from '@lunit/insight-viewer';
import React, { useMemo } from 'react';
import { render } from 'react-dom';
import { GlobalStyle } from './components/GlobalStyle';

installWADOImageLoader();

function App() {
  const image: CornerstoneImage = useMemo(
    () =>
      new CornerstoneSingleImage(`wadouri:https://lunit-io.github.io/frontend-fixtures/dcm-files/series/CT000010.dcm`, {
        unload: unloadImage,
      }),
    [],
  );

  return (
    <div>
      <GlobalStyle />
      <InsightViewer
        width={700}
        height={600}
        invert={false} //  
        flip={false} //   
        pan={true} // Pan Interaction  
        adjust={false} // Adjust Interaction   (Pan    )
        zoom={true} // Zoom Interaction  
        resetTime={Date.now()} //    Pan, Adjust, Zoom   
        image={image}
        updateCornerstoneRenderData={() => {}}
      />
    </div>
  );
}

render(<App />, document.querySelector('#app'));

// Hot Module Replacement
if (process.env.NODE_ENV === 'development' && module.hot) {
  module.hot.accept();
}
