import { Observable } from 'rxjs';

export interface CornerstoneImage {
  /** cornerstoneImage.image.subscribe()  cornerstone.Image    */
  readonly image: Observable<cornerstone.Image | null>;
  /** cornerstoneImage.progress.subscribe()    0 ~ 1     */
  readonly progress: Observable<number>;
  /** cornerstoneImage.destroy()   Image Cache  */
  destroy: () => void;
}

export interface CornerstoneSequenceImage extends CornerstoneImage {
  /**    */
  length: () => number;
  /**    Image index number */
  getIndex: () => number;
  /** cornerstoneImage.index.subscribe()     Image index number    */
  readonly index: Observable<number>;
  /**  Image  */
  next: (delta?: number) => void;
  /**  Image  */
  prev: (delta?: number) => void;
  /**  index number Image  */
  goto: (index: number) => void;
}

/** @deprecated use CornerstoneSequenceImage */
export interface CornerstoneBulkImage extends CornerstoneSequenceImage {}

export interface ProgressEventDetail {
  url: string;
  imageId: string;
  loaded: number;
  total: number;
  percentComplete: number; // 0 ~ 100
}

export function getProgressEventDetail(event: Event): ProgressEventDetail | undefined {
  const detail: object | undefined = event['detail'];

  if (
    detail &&
    typeof detail['url'] === 'string' &&
    typeof detail['imageId'] === 'string' &&
    typeof detail['loaded'] === 'number' &&
    typeof detail['total'] === 'number' &&
    typeof detail['percentComplete'] === 'number'
  ) {
    return detail as ProgressEventDetail;
  }

  return undefined;
}

export interface LoadImageParams {
  imageId: string;
  options?: object;
}

export interface ImageLoader {
  loadImage: (params: LoadImageParams) => Promise<cornerstone.Image>;
}

export interface NpyImageInfo {
  id: string;
  windowCenter: number;
  windowWidth: number;
  sliceSpacing: number;
  rowPixelSpacing: number;
  columnPixelSpacing: number;
  slope?: number;
  intercept?: number;
  minPixelValue?: number;
  maxPixelValue?: number;
}
